@use 'helpers/variables' as v;

.registration-form {
    background-color: v.$white;
    display: flex;
    flex-direction: column;
    padding: 2rem;
    border-radius: 1rem;
    input {
        width: 100%;
        padding: 1rem;
        margin-bottom: 1.5rem;
        border: 1px solid v.$grayishBlue;
        border-radius: 0.5rem;
        font-weight: 700;
        .error{
            background-image: url(../../images/icon-error.svg);
            background-size: 8%;
            background-repeat: no-repeat;
            background-position: 95% 50%;
        }


        &:last-of-type{
            margin-bottom: 0;
            cursor: pointer;
            background-image: none;
        }
    }
}

.button {
    color: v.$white;
    text-transform: uppercase;
    border: none;
    background-color: v.$green;
    &:hover {
        opacity: 75%;
    }
}
.notice {
    padding: 0 2.5rem;
    font-size: 1rem;
    line-height: 2rem;
    color: v.$grayishBlue;
    span {
        color: v.$red;
        font-weight: 700;
        cursor: pointer;
    }
}