.attribution {
  margin-top: 2rem;
  font-size: 11px;
  text-align: center;
  color: #ffffff;
}
.attribution a {
  color: #ffffff;
  text-decoration: none;
}

.container {
  height: 100vh;
  margin: 0 auto;
  width: 31rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.marketing {
  color: #ffffff;
}

.registration-info {
  padding: 0.2rem;
  background-color: hsl(248deg, 32%, 49%);
  color: #ffffff;
  margin-top: 4rem;
  margin-bottom: 3rem;
  border-radius: 1rem;
}
.registration-info span {
  font-weight: 700;
}

.shadow {
  -webkit-box-shadow: 1px 10px 5px 0px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 1px 10px 5px 0px rgba(0, 0, 0, 0.2);
  box-shadow: 1px 10px 5px 0px rgba(0, 0, 0, 0.2);
}

.registration-form {
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  padding: 2rem;
  border-radius: 1rem;
}
.registration-form input {
  width: 100%;
  padding: 1rem;
  margin-bottom: 1.5rem;
  border: 1px solid hsl(246deg, 25%, 77%);
  border-radius: 0.5rem;
  font-weight: 700;
}
.registration-form input .error {
  background-image: url(../../images/icon-error.svg);
  background-size: 8%;
  background-repeat: no-repeat;
  background-position: 95% 50%;
}
.registration-form input:last-of-type {
  margin-bottom: 0;
  cursor: pointer;
  background-image: none;
}

.button {
  color: #ffffff;
  text-transform: uppercase;
  border: none;
  background-color: hsl(154deg, 59%, 51%);
}
.button:hover {
  opacity: 75%;
}

.notice {
  padding: 0 2.5rem;
  font-size: 1rem;
  line-height: 2rem;
  color: hsl(246deg, 25%, 77%);
}
.notice span {
  color: hsl(0deg, 100%, 74%);
  font-weight: 700;
  cursor: pointer;
}